.parent{
    margin-top: 30px;
    display: flex;
    justify-content: center;
}

.child{
    width: 60%;
}

.modalBody{
    width: 70%;
    display: flex;
    justify-content: center;
    margin-top: 90px;
    margin-left: 200px;

}
.table{
    width:100%
}
@media(max-width: 600px){
    .child{
        margin-left: 10px;
        margin-right: 10px;
        width: 100%;
    }
    .modalBody{
        width: 100%;
        margin-top: 90px;
        margin-left: 0px;
    }
    .table{
        width:95%
    }
}